  call2Clicker (host, participant) {
    // console.log(host + " & " + participant);
    // !this.state.VirtualClicker ? this.setState({'VirtualClicker': true}) : this.setState({'VirtualClicker': false});

    // if (host || participant || (host && participant)) {
    if (host === "active" || participant === "active" || (host === "active" && participant === "active")) {
        // this.setState({'VirtualClickerParams': {host: true}});
        let newState = this.state.VirtualClickerParams;
        newState.host = host;
        newState.participant = participant;
        this.setState({VirtualClickerParams: newState});

        this.setState({'VirtualClicker': true});
        // this.setState({'VirtualClickerParams': {participant: true}});
        // VirtualClickerContainer.host = arg1.bttn4;
    }
    else {
        this.setState({'VirtualClicker': false})
        this.setState({'VirtualClickerMaximized': true})
        // this.state.VirtualClickerMaximized
    }

  }

